
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: 'item',
components: {
Cell
},
props: ['item'],
data () {
return {
msg: 'main',
statusClass: {}
}
},
computed: {
imporcess () {
return this.item.status === 'SHIPMENT_INPROCESS'
},
statusLabel () {
let label = ''
if (this.item.pickUpStatus === 'LACK') {
label = ''
this.statusClass = {
'label-primary': true
}
} else if (this.item.status === 'NOT_SHIPMENT') {
label = ''
this.statusClass = {
'label-primary': true
}
} else if (this.item.status === 'SHIPMENT_INPROCESS') {
label = ''
this.statusClass = {
'label-inprocess': true
}
} else if (this.item.status === 'SHIPMENT_SUCCESS') {
label = ''
this.statusClass = {
'label-info': true
}
} else {

label = ''
this.statusClass = {
'label-fail': true
}
}
return label
}
}
}



